<template>
	<div class="saleedi-order-details">
		<div class="order-details-warp">
			<div class="order-details-box">
				<div class="order-details fx-edi-block">
					<div class="order-ent">企业名称：{{orderDetailsData.companyName}}</div>
					<div class="order-details-item">
						<div class="order-item-cell">订单编号：{{orderDetailsData.billNo}}</div>
						<div class="order-item-cell">收货机构：{{orderDetailsData.customerName}}</div>
						<div class="order-item-cell">明细数量：{{orderDetailsData.billDtCount}}</div>
						<div class="order-item-cell">数量合计：{{orderDetailsData.goodsCount}}</div>
						<div class="order-item-cell">金额总计：{{orderDetailsData.sumMoneyWithTax}}</div>
					</div>
					<van-button
						class="details-edit-btn"
						size="mini"
						type="primary"
						plain
						@click="detailsEditClick"
						v-show="orderDetailsData.billState === '1'"
					>修改</van-button>
					<van-button
						class="details-edit-btn"
						size="mini"
						type="primary"
						plain
						@click="watchEditClick"
						v-show="orderDetailsData.billState === '2' || orderDetailsData.billState === '3'"
					>查看</van-button>
				</div>
			</div>
			<div class="order-address fx-edi-block">
				<div class="order-address-cell">
					<div class="address-key">收货人：</div>
					<div>{{addressData.consignee}}</div>
				</div>
				<div class="order-address-cell">
					<div class="address-key">联系方式：</div>
					<div>{{addressData.telePhone}}</div>
				</div>
				<div class="order-address-cell">
					<div class="address-key">收货地址：</div>
					<div class="address-value">{{addressData.address}}</div>
				</div>
				<van-button
					class="address-edit-btn"
					size="mini"
					type="primary"
					plain
					@click="addressEditClick()"
					v-show="orderDetailsData.billState === '1'"
				>修改</van-button>
			</div>
			<div class="order-info fx-edi-block">
				<div class="infor-key">备注信息：</div>
				<van-field
					v-model="orderDetailsData.remark"
					maxlength="50"
					class="info-value"
					type="textarea"
					placeholder="请输入备注信息(最多输入50个字符)"
					rows="3"
					v-show="orderDetailsData.billState === '1'"/>
				<div class="info-value" v-show="orderDetailsData.billState !== '1'">{{orderDetailsData.remark}}</div>
			</div>
			<div class="order-file fx-edi-block">
				<div class="order-file-cell">
					<div class="file-key">附件资料：</div>
					<div>
						<label class="upload-label" for="file">
							<i class="upload-img-icon fl-l" v-show="orderDetailsData.billState === '1'"></i>
						</label>
						<i class="watch-file-icon fl-l" v-show="watchFileBtnShow" @click="onWatchClick"></i>
						<input type="file" ref="file" id="file" accept="image/*" mutiple="mutiple" v-show="false" @change="getFile">
					</div>
				</div>
			</div>
		</div>
		<div class="order-details-bottom">
			<div class="order-bottom-sumprice fx-price-font">￥{{orderDetailsData.sumMoneyWithTax}}</div>
			<van-button
				v-show="orderDetailsData.billState === '1'"
				type="primary"
				square
				@click="confirm">确认提交</van-button>
			<van-button
				v-show="orderDetailsData.billState === '2'"
				type="warning"
				square
				@click="receivingHandler">确认收货</van-button>
			<van-button
				v-show="orderDetailsData.billState === '3'"
				type="danger"
				square
				@click="buyAgainHandler">再来一单</van-button>
		</div>
	</div>
</template>

<script>
export default {
	name: 'orderDetails',
	data () {
		return {
			orderDetailsData: {},
			addressData: {},
			orderInfo: '',
			queryId: '',
			previewSrc: ''
		}
	},
	created () {
		this.$fxEventBus.$on('changeSelectAddress', (data) => {
			this.addressData = data
			this.orderDetailsData.receiptInfoId = data.id
		})
	},
	beforeRouteEnter (to, from, next) {
		if (from.path !== '/addressList') {
			next(vm => {
				vm.getBillAndDetails().then(res => {
					vm.previewSrc = ''
					if (res.receiptInfoId) {
						vm.getCurrentAddress(res.receiptInfoId)
					}
				})
			})
		} else {
			next()
		}
	},
	computed: {
		watchFileBtnShow () {
			return this.previewSrc !== '' ||
					this.orderDetailsData.billState === '2' ||
					this.orderDetailsData.billState === '3'
		}
	},
	methods: {
		getBillAndDetails () {
			return new Promise(resolve => {
				const params = {
					id: this.$route.query.id
				}
				this.$fxHttp.requestPost({
					url: `${this.$fxBaseApi.url}/getBillAndDetails`,
					data: params
				}).then(res => {
					this.orderDetailsData = res
					resolve(res)
				})
			})
		},
		getCurrentAddress (receiptInfoId) {
			const params = {
				id: receiptInfoId
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/getReceipt`,
				data: params
			}).then(res => {
				this.addressData = res
			})
		},
		detailsEditClick () {
			let companyId = this.orderDetailsData.companyId
			let companyName = this.orderDetailsData.companyName
			let planID = this.orderDetailsData.planId
			let rdcId = this.orderDetailsData.organId
			let rdcName = this.orderDetailsData.organName
			let receiptInfoId = this.orderDetailsData.receiptInfoId
			this.$router.push({
				path: '/orderItemDetails',
				query: {
					id: this.$route.query.id,
					companyId,
					companyName,
					planID,
					rdcId,
					rdcName,
					receiptInfoId,
					billState: '1'

				}
			})
		},
		watchEditClick () {
			let companyId = this.orderDetailsData.companyId
			let companyName = this.orderDetailsData.companyName
			let planID = this.orderDetailsData.planId
			let rdcId = this.orderDetailsData.organId
			let rdcName = this.orderDetailsData.organName
			let receiptInfoId = this.orderDetailsData.receiptInfoId
			let billState = this.orderDetailsData.billState
			this.$router.push({
				path: '/orderItemDetails',
				query: {
					id: this.$route.query.id,
					companyId,
					companyName,
					planID,
					rdcId,
					rdcName,
					receiptInfoId,
					billState

				}
			})
		},
		addressEditClick (defaultId) {
			this.$router.push({
				path: '/addressList',
				query: {
					select: true,
					defaultId
				}
			})
		},
		confirm () {
			const params = {
				id: this.orderDetailsData.id,
				remark: this.orderDetailsData.remark,
				consignee: this.addressData.consignee,
				telePhone: this.addressData.telePhone,
				address: this.addressData.address,
				region: this.addressData.region
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/comfirmBill`,
				data: params
			}).then(res => {
				this.sendBill()
				window.history.go(-1)
			})
		},
		sendBill () {
			const params = {
				id: this.orderDetailsData.id
			}
			this.$fxHttp.requestGet({
				url: `${this.$fxBaseApi.url}/sendBill`,
				data: params
			}).then(res => {
			})
		},
		receivingHandler () {
			const params = {
				id: this.orderDetailsData.id
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/completeBill`,
				data: params
			}).then(res => {
				this.$toast.success('确认收货成功')
				window.history.go(-1)
			})
		},
		buyAgainHandler () {
			console.log('再来一单')
		},
		onWatchClick () {
			var url = this.previewSrc === '' ? require('../../assets/images/img/order-img.jpg') : this.previewSrc
			this.$ImagePreview([
				url
			])
		},
		getFile (e) {
			let files = e.target.files[0]
			if (!files) {
				return false
			}
			let type = files.type
			let size = files.size / 1024 / 1024
			let reg = new RegExp(/image\/png|image\/jpg|image\/jpeg/)
			if (!reg.test(type)) {
				this.$toast.fail('上传图片只支持png,jpg,jpeg格式!')
				return false
			}
			if (size > 1) {
				// this.$toast.fail('上传图片大小不能超过1M!')
				// return false
			}
			if (!e || !window.FileReader) {
				this.$toast.fail('选取图片失败!')
				return false
			}
			let reader = new FileReader()
			reader.readAsDataURL(files)
			reader.onloadend = () => {
				let image = new Image()
				image.src = reader.result
				image.onload = () => {
					this.previewSrc = reader.result
				}
			}
		}
	}
}
</script>
