<template>
	<div class="saleedi-create-order">
		<div class="create-order-ent fx-edi-block">
			<div class="create-order-title">确认订货对象</div>
			<div class="ent-block">
				<div class="ent-title">{{this.$route.query.companyName}}</div>
				<div class="order-rdc-list-ul">
					<van-radio-group v-model="RDCValue" class="distribution-centers-list radio-list">
						<van-radio v-for="(item, index) in RDCList" :name="index" class="radio-item" :key="index">{{item.name}}</van-radio>
					</van-radio-group>
				</div>
			</div>
		</div>
		<div class="next-step">
			<i class="next-step-icon"></i>
		</div>
		<div class="create-order-plan fx-edi-block">
			<div class="create-order-title">确认订货方案</div>
			<div class="order-plan-list-ul">
				<van-radio-group v-model="planValue" class="order-plan-list radio-list">
					<van-radio v-for="(item, index) in planList" :name="index" class="radio-item" :key="index">{{item.planName}}</van-radio>
				</van-radio-group>
			</div>
		</div>
		<div class="create-order-bottom">
			<van-button class="begin-order-btn" type="primary" round @click="beginOrder">开始订货</van-button>
		</div>
	</div>
</template>

<script>

export default {
	name: 'createOrder',
	data () {
		return {
			RDCValue: '',
			planValue: '',
			RDCList: [],
			planList: []
		}
	},
	created () {
		this.getAllRDC()
	},
	watch: {
		RDCValue (val) {
			let rdcId = this.RDCList[val].id
			this.getAllOrderPlan(rdcId)
		}
	},
	methods: {
		getAllRDC () {
			const params = {
				companyName: this.$route.query.companyName
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/getAllDC`,
				data: params
			}).then(res => {
				this.RDCList = res
			})
		},
		getAllOrderPlan (sellerId) {
			const params = {
				companyName: this.$route.query.companyName,
				sellerId
			}
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/getAllOrderPlan`,
				data: params
			}).then(res => {
				this.planList = res
			})
		},
		beginOrder () {
			if (
				this.RDCList.length === 0 ||
				this.planList.length === 0 ||
				this.RDCValue === '' ||
				this.planValue === ''
			) {
				return false
			}
			let companyId = this.$route.query.companyId
			let companyName = this.$route.query.companyName
			let planID = this.planList[this.planValue].id
			let arriveDateInterval = this.planList[this.planValue].arriveDateInterval
			let rdcId = this.RDCList[this.RDCValue].id
			let rdcName = this.RDCList[this.RDCValue].name
			this.$router.push({
				path: '/orderItemDetails',
				query: {
					companyId,
					companyName,
					planID,
					rdcId,
					rdcName,
					arriveDateInterval
				}
			})
		}
	}
}
</script>
